import React, { useContext, useState, useRef } from 'react';
import Button from '../button/';
import { ContextApp } from '../../reducer/index';
import { useClickOutside } from '../../utils';
import './auth-form.css';

const AuthForm = ({ closeModal }) => {
  const ref = useRef();
  const { state, dispatch } = useContext(ContextApp);
  const [name, setName] = useState(state.save ? state.name : '');
  const [password, setPassword] = useState('');
  const [checked, setChecked] = useState(false);
  const [nameMessage, setNameMessage] = useState('–õ–æ–≥–∏–Ω');
  const [passwordMessage, setPasswordMessage] = useState('–ü–∞—Ä–æ–ª—å');

  useClickOutside(ref, closeModal);
  const login = (e) => {
    e.preventDefault();
    const regName = new RegExp(/^[a-zA-Z–∞-—è–ê-—è0-9]/g);
    const regPassword = new RegExp(/^[a-zA-Z–∞-—è–ê-–Ø0-9]{6,}$/g);
    const validationName = regName.test(name);
    const validationPassword = regPassword.test(password);
    if (validationName && validationPassword) {
      dispatch({
        type: 'LOGIN',
        payload: {
          name: name,
          save: checked ? true : '',
        },
      });
    } else {
      if (!validationName) {
        setName('');
        setNameMessage('–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –ª–æ–≥–∏–Ω');
      }
      if (!validationPassword) {
        setPassword('');
        setPasswordMessage('–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –ø–∞—Ä–æ–ª—å');
      }
    }
  };
  return (
    <form className="login column" onSubmit={login} ref={ref}>
      <div className="login__form">
        <div className="login__form__title">–í—Ö–æ–¥</div>
        <input
          type="text"
          placeholder={nameMessage}
          className="name input"
          value={name}
          onChange={(e) => {
            setName(e.target.value);
          }}
          autoFocus
        />
        <input
          type="text"
          placeholder={passwordMessage}
          className="password input"
          value={password}
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        />
        <div className="login__checkbox">
          <input
            type="checkbox"
            id="checkbox-1"
            name="check-box"
            onChange={() => {
              setChecked(!checked);
            }}
          />
          <label className="checkbox__input" htmlFor="checkbox-1">
            <span className="checkbox__image"></span>
            <label className="checkbox__label" htmlFor="checkbox-1">
              –ó–∞–ø–æ–º–Ω–∏—Ç—å
            </label>
          </label>
        </div>
      </div>
      <Button type="submit" className="login__form__button">
        –í–æ–π—Ç–∏
      </Button>
    </form>
  );
};

export default AuthForm;
